html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 10% auto;
  padding-left: 40px;
  padding-right: 40px;
}

.row {
  margin-left: -40px;
  margin-right: -40px;
}

body {
  background-image: url(/img/Pattern.png);
  font-family: 'Exo 2', sans-serif;
}

.container {
  text-align: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.title {
  font-family: "Exo 2";
  font-size: 36px;
  line-height: 1;
  color: #f2f2f2;
  font-weight: 50;
  margin: 0 auto;
}

.item__block {
  width: 320px;
  margin: 40px;
}

.items {
  background-color: #f2f2f2;
  background-image: url(/img/Photo.png);
  background-repeat: no-repeat;
  background-position: -26px 202px;
  width: 100%;
  height: 480px;
  position: relative;
  border: 4px solid #1698d9;
  border-radius: 10px;
  position: relative;
  padding: 16px 48px;
  cursor: pointer;
  text-align: left;
}

.items .cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 15;
}

.item__title {
  font-family: "Exo 2";
  font-size: 46px;
  line-height: 1;
  color: #000000;
  font-weight: 700;
  margin-top: 14px;
}

.item__options {
  font-family: "Exo 2";
  font-size: 26px;
  line-height: 1;
  color: #000000;
  font-weight: 700;
  margin-top: 5px;
}

.item__text {
  font-size: 14px;
  color: #666666;
}

.item__gift {
  font-size: 14px;
  color: #666666;
  margin-top: 15px;
}

.item__weight {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #1698d9;
  position: absolute;
  z-index: 10;
  text-align: center;
  position: absolute;
  bottom: 8px;
  right: 10px;
  padding-top: 8px;
}

.item__weight .weight__count {
  font-family: "Exo 2";
  font-size: 42px;
  line-height: 1;
  color: #f2f2f2;
}

.item__weight .weight {
  font-family: "Exo 2";
  font-size: 16px;
  line-height: 1;
  color: #f2f2f2;
}

.item__weight span {
  display: block;
}

.item__order {
  font-family: "Exo 2";
  font-size: 12px;
  line-height: 1;
  color: #f2f2f2;
  display: inline-block;
  margin-top: 12px;
}

.order {
  font-family: "Exo 2";
  font-size: 12px;
  line-height: 1;
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  display: inline-block;
  cursor: pointer;
  margin-left: 3px;
  margin-top: 12px;
}

.item__order-done {
  font-family: "Exo 2";
  font-size: 12px;
  line-height: 1;
  color: #f2f2f2;
  margin-top: 12px;
  display: none;
}

.item__active {
  border: 4px solid #d91667;
}

.item__weight-active {
  background: #d91667;
}

.item__passive {
  border: 4px solid #666666;
}

.item__weight-passive {
  background: #666666;
}

.display-none {
  display: none;
}

.order__passive {
  color: #ffff66;
}

@media (max-width: 1200px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .row {
    margin-left: -15px;
    margin-right: -15px;
  }

  .item__block {
    margin-left: 15px;
    margin-right: 15px;
  }
}